import React from "react";
import FullCalendar from "@fullcalendar/react"
import googleCalendarPlugin from "@fullcalendar/google-calendar"
import listPlugin from '@fullcalendar/list';
import base64 from 'react-native-base64'


const Calendar = () => {
  const calendar = base64.decode("QUl6YVN5RGxTTGVHQlRHanNIVVc0YWNrZE5jUHktcDgyRnlwcjhv");

  return (
<>
<div className="shane_tm_section" id="calendar">
        <div className="shane_tm_portfolio">
          <div className="container">
            <div className="positon-relative">
              <div className="shane_tm_title">
                <div className="title_flex">
                  <div className="left">
                    <span>Upcoming Events</span>
                    <br></br>
                  </div>
                </div>
              </div>
              </div>
              <section id="calendar" className="calendar" >
        <div className="calendar container">
 <div>
          <div className="calendar">
            
            <div className="calendar">
            <FullCalendar
      plugins={[listPlugin, googleCalendarPlugin]}
      initialView="listYear"
      googleCalendarApiKey={calendar}
      events={{
        googleCalendarId:
          "dvcidqjia7p5j39qq43km7t5vs@group.calendar.google.com", // (molly)auo96fp77ftokmft0ih4kp8598@group.calendar.google.com
      }}
      displayEventTime={true}
      eventColor={"#f44880"}
      listDayFormat={{ // will produce something like "Tuesday, September 18, 2018"
        month: 'long',
        year: 'numeric',
        day: 'numeric',
        weekday: 'long'
      }}
      listDaySideFormat={false}
      height={600}
      contentHeight={600}
      expandRows={true}
      validRange={{
        start: new Date().toISOString().split("T")[0]
      }}/>
            </div>
          </div>
  </div>
  </div>
</section>
              </div>
              </div>
              </div>
</>
  );
};

export default Calendar;
