import React from "react";

const AboutTwo = () => {
  return (
    <>
      <div className="shane_tm_section" id="merch">
        <div className="shane_tm_about">
          <div className="container">
            <div className="about_inner">
              <div className="left">
                <div
                  className="image"
                  data-aos="fade-up"
                  data-aos-duration="1200"
                >
                  <img src="/img/placeholders/1-1.jpg" alt="placeholder" />

                  <div
                    className="main"
                    style={{
                      backgroundImage: `url(${
                        process.env.PUBLIC_URL + "img/about/merch.jpg"
                      })`,
                    }}
                  ></div>
                </div>
                {/* End image */}
              </div>
              {/* End left */}

              <div className="right">
                <div
                  className="text"
                >
                  <p><center>Click the button below to view Molly's Merch Shop</center></p>
                </div>
                <div
                  className="shane_tm_button"
                >
                  <center><a href="https://google.com">
                    Merch Shop
                  </a></center>
                </div>
              </div>
              {/* End right */}
            </div>
          </div>
          {/* End container */}
        </div>
      </div>
    </>
  );
};

export default AboutTwo;
