import React, { useState } from "react";
import Modal from "react-modal";
import Map from "../Map";
import Contact from "../Contact";
import Social from "../Social";

Modal.setAppElement("#root");

const CallToActionTwo = () => {
  const [isOpen, setIsOpen] = useState(false);

  function toggleModalOne() {
    setIsOpen(!isOpen);
  }

  return (
    <div className="shane_tm_section" id="booking">
      <div
        className="shane_tm_talk bg_image_props"
      >
        <div className="shape">
          <img className="svg" src="/img/svg/paper.svg" alt="partners brand" />
        </div>
        {/* End shape */}
        <div className="talk_inner">
          <div className="text" data-aos="fade-up" data-aos-duration="1200">
            <h3>Booking Information</h3>
          </div>
          <div
                  className="text"
                  data-aos="fade-up"
                  data-aos-duration="1200"
                >
                  
                  With nearly a decade of experience, Molly is known for her professionalism and distinct brand of drag.
                  In addition to performing and emceeing at bars and special events across the country, Molly is also the
                  founder of Hell in Heels Productions!
                  <br></br>
                  <br></br>
                  With a degree in professional party planning and a passion for entertaining audiences of all types and
                  sizes, contact Molly for all your entertainment needs - emceeing, lip syncing, stand-up comedy, private
                  parties, wedding officiant, and more!
                  <br></br>
                  <br></br>
                  Interested in booking Molly for an event?
                  
                </div>

          <div
            className="button"
            data-aos="fade-up"
          >
            <a href="mailto:booking@mollymormen.com">
            <button className="white-fill-bg">
              Email: booking@mollymormen.com
            </button></a>
          </div>
        </div>
        {/* End talk_inner */}
      </div>
      {/* Start Modal */}

    </div>
  );
};

export default CallToActionTwo;
