import React from "react";

const AboutTwo = () => {
  return (
    <>
      <div className="shane_tm_section" id="about">
        <div className="shane_tm_about">
          <div className="container">
            <div className="about_inner">
              <div className="left">
                <div
                  className="image"
                  data-aos="fade-up"
                  data-aos-duration="1200"
                >
                  <img src="/img/placeholders/1-1.jpg" alt="placeholder" />

                  <div
                    className="main"
                    style={{
                      backgroundImage: `url(${
                        process.env.PUBLIC_URL + "img/about/2.jpg"
                      })`,
                    }}
                  ></div>
                </div>
                {/* End image */}
              </div>
              {/* End left */}

              <div className="right">
                <div
                  className="shane_tm_title"
                  data-aos="fade-up"
                  data-aos-duration="1200"
                >
                  <span>About Molly</span>
                  {/* <h3>Drag Queen based in Cincinnati, Ohio</h3> */}
                </div>
                <div
                  className="text"
                  data-aos="fade-up"
                  data-aos-duration="1200"
                >
                  <p>
                  <b>Molly Mormen</b> is a Cincinnati staple who performs extensively throughout the Midwest and across the
                  United States and Canada. Queen, empress, and sister, she's an original Cincinnati 3-way!</p>
                  <br></br>
                  <p>
                  Equal parts camp and kindness, <b>Molly</b> is known to do anything for a dollar. Ask to see her worm or a
                  magic trick - you never know when her eyebrows will disappear. This quick-tongued beauty is here to
                  make you laugh and can't be topped.</p>
                  <br></br>
                  <p>
                  Growing up Mormon in Utah fostered a passion for helping others live, laugh and love! Her feel-good
                  shows are sure to bless your life (or at least your heart) and keep you coming back for more! When she's
                  not sponsoring and emceeing charity events or eating tacos, you can catch her every weekend at e|19
                  hosting <b>Frisky Fridays</b> and <b>Gay Church</b> on Sundays, and on Saturdays at other venues throughout the
                  region.
                  </p>
                </div>
                <div
                  className="shane_tm_button"
                >
                  <a href="#booking">
                    Book Molly for an event
                  </a>
                </div>
              </div>
              {/* End right */}
            </div>
          </div>
          {/* End container */}
        </div>
      </div>
    </>
  );
};

export default AboutTwo;
